@charset "UTF-8";
::-moz-selection {
  color: #fff;
  background: #007DC6;
}

::selection {
  color: #fff;
  background: #007DC6;
}

body {
  font-family: "MiraiHuman-font", sans-serif;
  color: #666666;
  font-weight: 400;
  font-size: 16px;
  overflow-x: hidden;
}

a {
  text-decoration: none;
}

.title {
  font-weight: 900;
  font-size: 28px;
  line-height: 1.5;
  text-transform: uppercase;
  color: #007DC6;
}

.sub-title {
  font-weight: 700;
  font-size: 24px;
  text-transform: uppercase;
}

.color-title {
  color: #007DC6;
}

.fw-700 {
  font-weight: 700;
}

.text-center {
  text-align: center;
}

.see-more {
  display: inline-block;
  margin-top: 5px;
  color: #007DC6;
}
.see-more:after {
  content: "\f054";
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  transform: translate(25%, 25%);
}

.hot,
.new {
  position: relative;
}
.hot:after,
.new:after {
  text-transform: lowercase;
  content: "hot";
  font-weight: 900;
  color: red;
  font-size: 14px;
  position: absolute;
  margin-left: 2px;
  bottom: 5px;
}

.new:after {
  content: "new";
}

.tabs-pagination {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}
.tabs-pagination a {
  line-height: 30px;
  display: block;
  margin: 0 10px;
  color: #666666;
}
.tabs-pagination a:last-child {
  margin-right: 0;
}
.tabs-pagination a.isDisabled {
  color: #DDDDDD;
  pointer-events: none;
}
.tabs-pagination a.current-page {
  position: relative;
  color: #fff;
}
.tabs-pagination a.current-page:before {
  content: "";
  z-index: -1;
  width: 20px;
  height: 20px;
  background: url("../img/Polygon 1.png");
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.posted {
  margin-top: 7px;
  padding-left: 15px;
  font-size: 12px;
  position: relative;
}
.posted:before {
  content: "\f150";
  display: inline-block;
  font-family: "Material Design Icons";
  font-size: inherit;
  text-rendering: auto;
  line-height: 1;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  position: absolute;
  top: 0;
  left: 0;
}

.line {
  margin: 30px 0 10px;
  width: 100%;
  height: 2px;
  background: #F1F1F1;
}

.navbar-center {
  margin: 15px 0 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.navbar-center a {
  color: #666666;
}
.navbar-center li {
  list-style: none;
  background: #F1F1F1;
  padding: 10px 15px;
  text-transform: uppercase;
  font-weight: 700;
  transition: all 0.1s ease-in-out;
}
.navbar-center li:not(:first-child) {
  margin-left: 2px;
}
.navbar-center li.active, .navbar-center li:hover {
  background: #007DC6;
}
.navbar-center li.active a, .navbar-center li:hover a {
  color: #fff;
}

.form-item {
  width: 100%;
  margin-bottom: 15px;
}
.form-item label {
  color: #007DC6;
}
.form-item input, .form-item textarea {
  margin-top: 7px;
  width: 100%;
  padding: 8px 15px;
  background: #F1F1F1;
  border: 1px solid #B9B9B9;
  border-radius: 4px;
  font-family: "MiraiHuman-font", sans-serif;
  font-weight: 700;
  color: #007DC6;
}
.form-item input::placeholder, .form-item textarea::placeholder {
  color: #B9B9B9;
  font-weight: 400;
}
.form-item textarea {
  height: 150px;
}

.flex-box {
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

.transformY-center {
  position: relative;
  top: 50%;
  transform: translateY(-50%);
  z-index: 1;
}

@media (max-width: 991.97px) {
  body {
    font-size: 15px;
  }

  .title {
    font-size: 24px;
  }

  .sub-title {
    font-size: 18px;
  }

  .tabs-pagination {
    justify-content: center;
  }
}
@media (max-width: 767.97px) {
  body {
    font-size: 14px;
  }

  .title {
    font-size: 18px;
  }

  .sub-title {
    font-size: 16px;
  }

  .navbar-center {
    flex-wrap: wrap;
    margin-bottom: 15px;
  }
  .navbar-center li {
    margin-bottom: 2px;
  }
}
header {
  background: #007DC6;
  z-index: 1000;
  position: sticky;
  top: 0;
  left: 0;
}

.header-top {
  background: #ffffff;
  position: relative;
}
.header-top:before {
  content: "";
  position: absolute;
  top: calc(100%-1px);
  left: 0;
  background: url("../img/bg-top-header.png");
  width: 100%;
  height: 15px;
}

.header-top-inner,
.header-bottom-inner {
  display: flex;
  flex: 0 0 100%;
  max-width: 100%;
}

.header-top-inner {
  width: 100%;
  height: 40px;
  justify-content: space-between;
  position: relative;
  z-index: 2;
}

.header-left,
.header-right {
  display: flex;
  align-items: center;
}

.header-left {
  position: relative;
  font-size: 14px;
}
.header-left p:nth-child(2) {
  margin-left: 10px;
}
.header-left span.mdi {
  position: absolute;
}
.header-left span:nth-child(2) {
  margin-left: 20px;
}

.header-logo {
  position: absolute;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
}
.header-logo img:last-child {
  display: none;
}

.language-bar a {
  display: inline-block;
}
.language-bar a:nth-child(2) {
  margin-left: 10px;
}

.search-box {
  display: flex;
  margin-right: 15px;
}
.search-box button {
  border: none;
  background: none;
  font-size: 24px;
  color: #D9D9D9;
  position: relative;
  margin-left: 15px;
}
.search-box button:before {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.search-box input {
  font-family: "MiraiHuman-font", sans-serif;
  font-size: 16px;
  width: 230px;
  padding: 0 10px;
  border: none;
  background: none;
  box-shadow: 0 1px 0 0px #D9D9D9;
}
.search-box input::placeholder {
  color: #D9D9D9;
}
.search-box input:focus {
  outline: none;
  color: #666666;
  box-shadow: 0 2px 0 0px #38A8D9;
}

.header-bottom a {
  display: inline-block;
  color: #fff;
  font-weight: 600;
  text-decoration: none;
  padding-bottom: 3px;
}
.header-bottom a:not(:first-child) {
  margin-left: 15px;
}
.header-bottom a:hover, .header-bottom a.active {
  transition: all 0.3s ease-in-out;
  box-shadow: 0 2px 0 0px #fff;
}

.header-bottom-inner {
  z-index: 2;
  height: 60px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.header-bottom-inner .navbar-left a:first-child span:last-child {
  display: none;
}

.toggle-menu {
  display: none;
}

@media (max-width: 1199.97px) {
  .header-top > img {
    height: 80px;
  }

  .header-logo {
    transform: translate(-50%, 11px);
  }
  .header-logo img {
    width: 180px;
  }

  .header-top-inner {
    height: 32px;
  }

  .header-left {
    font-size: 14px;
  }

  .search-box {
    margin-right: 5px;
  }
  .search-box input {
    font-size: 14px;
    width: 200px;
  }
  .search-box button {
    transform: scale(0.85);
  }

  .language-bar img {
    transform: scale(0.85);
  }
  .language-bar a:nth-child(2) {
    margin-left: 0;
  }

  .header-bottom a {
    font-size: 15px;
  }
  .header-bottom a:not(:first-child) {
    margin-left: 10px;
  }

  .header-bottom-inner {
    height: 45px;
  }
}
@media (min-width: 768.97px) and (max-width: 991.97px) {
  .header-left {
    display: block;
  }
  .header-left p:nth-child(2) {
    margin: 0;
  }

  .search-box input {
    width: 150px;
  }
  .search-box button {
    transform: scale(0.75);
  }

  .language-bar img {
    transform: scale(0.75);
  }

  .header-bottom a:not(:first-child) {
    margin-left: 15px;
  }

  .header-bottom-inner {
    height: 55px;
  }

  .navbar-left {
    width: 35%;
  }
  .navbar-left a {
    margin-bottom: 5px;
  }
  .navbar-left a:last-child {
    margin-left: 0;
  }

  .navbar-right {
    width: 40%;
    text-align: right;
  }
  .navbar-right a {
    margin-bottom: 5px;
  }
}
@media (max-width: 768px) {
  header {
    background: #fff;
  }

  .header-top-inner {
    justify-content: center;
  }

  .header-left {
    color: #fff;
    font-size: 15px;
    margin-top: 15px;
    display: block;
  }
  .header-left p:nth-child(2) {
    margin-left: 0;
    margin-top: 10px;
  }

  .header-logo {
    position: static;
    transform: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .header-logo img:first-child {
    display: none;
  }
  .header-logo img:last-child {
    display: block;
    width: 150px;
  }

  .header-bottom {
    background: #38a8d9;
  }
  .header-bottom a:not(:first-child) {
    margin: 0;
  }

  .header-top-inner {
    height: 60px;
    padding-left: 15px;
    padding-right: 15px;
  }

  .header-bottom-inner {
    display: none;
    height: auto;
    padding-bottom: 30px;
    padding-left: 15px;
    padding-right: 15px;
    position: absolute;
    top: 100%;
    left: 0;
    width: 100%;
    background: #38a8d9;
  }
  .header-bottom-inner nav {
    display: flex;
    flex-direction: column;
  }
  .header-bottom-inner nav a {
    display: inline-block;
    padding: 10px 0;
  }
  .header-bottom-inner .navbar-left a:first-child span:last-child {
    display: block;
  }
  .header-bottom-inner .navbar-left a:first-child span:first-child {
    display: none;
  }

  .search-box {
    margin: 0;
  }
  .search-box button {
    color: #fff;
  }
  .search-box input {
    font-size: 15px;
    padding-top: 5px;
    padding-bottom: 5px;
    box-shadow: 0 1px 0 0px #fff;
  }
  .search-box input::placeholder {
    color: #fff;
  }
  .search-box input:focus {
    color: #fff;
    box-shadow: 0 2px 0 0px #fff;
  }

  .language-bar a:hover {
    box-shadow: none;
  }

  .toggle-menu {
    display: block;
    margin-left: auto;
    width: 60px;
    height: 45px;
    position: relative;
    transform: rotate(0deg) scale(0.45);
    transition: 0.5s ease-in-out;
    cursor: pointer;
  }
  .toggle-menu span {
    display: block;
    position: absolute;
    height: 9px;
    width: 50%;
    background: #fff;
    opacity: 1;
    transform: rotate(0deg);
    transition: 0.25s ease-in-out;
  }
  .toggle-menu span:nth-child(even) {
    left: 50%;
    border-radius: 0 9px 9px 0;
  }
  .toggle-menu span:nth-child(odd) {
    left: 0;
    border-radius: 9px 0 0 9px;
  }
  .toggle-menu span:nth-child(1), .toggle-menu span:nth-child(2) {
    top: 0;
  }
  .toggle-menu span:nth-child(3), .toggle-menu span:nth-child(4) {
    top: 18px;
  }
  .toggle-menu span:nth-child(5), .toggle-menu span:nth-child(6) {
    top: 36px;
  }
  .toggle-menu.open span:nth-child(1), .toggle-menu.open span:nth-child(6) {
    transform: rotate(45deg);
  }
  .toggle-menu.open span:nth-child(2), .toggle-menu.open span:nth-child(5) {
    transform: rotate(-45deg);
  }
  .toggle-menu.open span:nth-child(1) {
    left: 5px;
    top: 7px;
  }
  .toggle-menu.open span:nth-child(2) {
    left: calc(50% - 5px);
    top: 7px;
  }
  .toggle-menu.open span:nth-child(3) {
    left: -50%;
    opacity: 0;
  }
  .toggle-menu.open span:nth-child(4) {
    left: 100%;
    opacity: 0;
  }
  .toggle-menu.open span:nth-child(5) {
    left: 5px;
    top: 29px;
  }
  .toggle-menu.open span:nth-child(6) {
    left: calc(50% - 5px);
    top: 29px;
  }
}
footer {
  padding-top: 30px;
  background: #F1F1F1;
  color: #888;
}
footer a {
  color: #888;
}
footer .logo {
  width: 150px;
  margin-bottom: 15px;
}
footer .item {
  margin-bottom: 20px;
}
footer .footer-title {
  font-weight: 900;
  font-size: 18px;
  color: #666666;
  text-transform: uppercase;
  margin-bottom: 10px;
}
footer .footer-content {
  line-height: 1.5;
}
footer .footer-content p {
  position: relative;
  padding-left: 20px;
}
footer .footer-content p:before {
  position: absolute;
  top: 1px;
  left: 0;
}
footer .footer-content img {
  margin-top: 15px;
  height: 40px;
}
footer .copyright {
  box-shadow: 0 -1px 0 0 #E1E1E1;
}
footer .copyright-inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
footer .social {
  height: 40px;
}
footer .social a {
  display: inline-block;
  width: 40px;
  height: 40px;
  position: relative;
  transition: all 0.15s ease-in-out;
  border-right: 1px solid #E1E1E1;
}
footer .social a:hover {
  color: #007DC6;
}
footer .social a:first-child {
  border-left: 1px solid #E1E1E1;
}
footer .social a:before {
  position: absolute;
  top: 50%;
  left: 50%;
  font-size: 24px;
  transform: translate(-50%, -50%);
}

@media (max-width: 575.97px) {
  footer .footer-title {
    font-size: 16px;
  }
  footer .copyright-inner {
    flex-direction: column;
    justify-content: center;
  }
  footer .social {
    margin-top: 10px;
  }
  footer .social a {
    border: none !important;
  }
}
.home-banner {
  position: relative;
  box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.3);
}
.home-banner .pagination {
  z-index: 1;
  position: absolute;
  bottom: 15px;
  left: 50%;
  transform: translateX(-50%);
}
.home-banner .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  border-radius: 0;
  transform: rotate(45deg);
  background: #000;
  opacity: 0.5;
  outline: none;
}
.home-banner .swiper-pagination-bullet:not(:first-child) {
  margin-left: 15px;
}
.home-banner .swiper-pagination-bullet-active {
  background: #38A8D9;
  opacity: 1;
}
.home-banner .img-wrap {
  height: 400px;
  overflow: hidden;
  position: relative;
}
.home-banner .img-wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.home-banner .img-wrap:before, .home-banner .img-wrap:after {
  content: "";
  position: absolute;
  top: 0;
  left: -50%;
}
.home-banner .img-wrap.banner-img-1:before {
  width: 100%;
  height: 100%;
  transform: skew(-30deg);
  background-color: #fff;
}
.home-banner .img-wrap.banner-img-1:after {
  top: 0;
  left: -2px;
  width: 55%;
  height: 200px;
  background-image: url(../img/bg-1.png);
  background-size: cover;
}
.home-banner .img-wrap.banner-img-2:before {
  z-index: 1;
  opacity: 0.8;
  background-image: url(../img/bg-2-1.png);
  background-size: cover;
  width: 80%;
  height: 100%;
  left: 50%;
  transform: translateX(-50%);
}
.home-banner .img-wrap.banner-img-2:after {
  left: 0;
  bottom: 0;
  right: 0;
  background: rgba(0, 0, 0, 0.4);
}
.home-banner .img-wrap.banner-img-3:before {
  background-image: url(../img/bg-3.png);
  background-size: cover;
  background-repeat: no-repeat;
  top: 50%;
  left: auto;
  right: 0;
  width: 70%;
  height: calc(100% + 2px);
  transform: translateY(-50%);
}

.banner-inner {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.banner-title {
