@media (max-width: 1199.97px) {
  .home-banner .img-wrap.banner-img-1:after {
    width: 100%;
    transform: translateX(-42%);
  }
  .home-banner .img-wrap.banner-img-2:before {
    width: 100%;
    height: 100%;
    left: 50%;
    transform: translateX(-50%);
  }

  .banner-item-1 .title {
    font-size: 60px;
  }
  .banner-item-1 .banner-title {
    font-size: 30px;
  }
  .banner-item-1 .flex-box div:nth-child(1) {
    font-size: 100px;
  }
  .banner-item-1 .flex-box div:nth-child(2) {
    font-size: 16px;
  }

  .banner-item-2 .title {
    font-size: 30px;
  }
  .banner-item-2 .banner-title {
    font-size: 40px;
  }
  .banner-item-2 .line {
    margin-top: 15px;
  }

  .banner-item-3 .title {
    font-size: 36px;
  }
}
@media (max-width: 991.97px) {
  .home-banner .img-wrap {
    height: 300px;
  }
  .home-banner .img-wrap.banner-img-1:after {
    height: 150px;
    transform: translateX(-44%);
  }

  .banner-item-1 .title {
    font-size: 40px;
  }
  .banner-item-1 .banner-title {
    font-size: 24px;
    height: 150px;
  }
  .banner-item-1 .flex-box div:nth-child(1) {
    font-size: 60px;
  }
  .banner-item-1 .flex-box div:nth-child(2) {
    font-size: 15px;
  }

  .banner-item-2 .banner-title {
    font-size: 32px;
  }

  .banner-item-3 .title {
    font-size: 28px;
  }
  .banner-item-3 .banner-title {
    font-size: 24px;
  }
  .banner-item-3 .banner-content {
    margin-top: 15px;
    font-size: 18px;
  }
}
@media (max-width: 767.97px) {
  .home-banner .img-wrap.banner-img-1:before {
    left: -45%;
  }
  .home-banner .img-wrap.banner-img-1:after {
    transform: translateX(-35%);
  }
