  width: 60px;
  height: 60px;
  position: fixed;
  background: #FF0000;
  border-radius: 99px;
  bottom: 50%;
  transform: translateY(-50%);
  right: 15px;
  cursor: pointer;
  animation: pulse 1.5s infinite;
}
.contact-now .btn-contact-now:before {
  color: #fff;
  position: absolute;
  font-size: 30px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  content: "\f3f6";
  display: inline-block;
  font-family: "Material Design Icons";
  text-rendering: auto;
  line-height: 1;
