@keyframes pulse {
  0% {
    transform: scale(1) rotate(10deg);
  }
  25% {
    transform: scale(1.1) rotate(0);
  }
  50% {
    transform: scale(1) rotate(-10deg);
  }
  75% {
    transform: scale(1) rotate(0deg);
  }
  100% {
    transform: scale(1) rotate(10deg);
  }
}
@keyframes circle-animation {
  0% {
    opacity: 0.5;
  }
  25% {
    transform: scale(1.25);
    opacity: 0.4;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.3;
  }
  75% {
    transform: scale(1.75);
    opacity: 0.1;
  }
  100% {
    transform: scale(2);
    opacity: 0;
  }
}
