}

.tuyen-dung-popup-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1999;
  background: rgba(0, 0, 0, 0.5);
  display: none;
}
.tuyen-dung-popup-background.active {
  display: block;
}

.tuyen-dung-popup {
  width: 350px;
  background: #ffffff;
  display: none;
  z-index: 2000;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.tuyen-dung-popup.active {
  display: block;
}
.tuyen-dung-popup .tuyen-dung-form {
  padding: 15px;
}
