  padding: 0 15px;
  line-height: 50px;
  border-bottom: 2px solid #007DC6;
  position: relative;
}
.tuyen-dung-popup .sub-title .mdi {
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: 15px;
  transform: translateY(-50%);
}
.tuyen-dung-popup .form-item {
  margin-bottom: 5px;
}
.tuyen-dung-popup .form-item textarea {
  height: 100px;
}
.tuyen-dung-popup .form-item:last-child {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.tuyen-dung-popup .custom-input-file {
  display: flex;
  align-items: center;
  position: relative;
}
.tuyen-dung-popup .custom-input-file input {
  position: absolute;
  outline: none;
  left: 45px;
  width: 85%;
}
.tuyen-dung-popup .custom-input-file input + label {
  order: -1;
  z-index: 1;
  padding: 4px 15px;
  line-height: 1;
  white-space: nowrap;
  border: 1px dashed #B9B9B9;
  border-radius: 4px;
  background: #f1f1f1;
  display: inline-block;
  font-family: "Darker Grotesque", sans-serif;
  color: #666666;
  font-size: 16px;
  cursor: pointer;
}
.tuyen-dung-popup .custom-input-file input + label * {
  pointer-events: none;
}
.tuyen-dung-popup .custom-input-file input:focus + label,
.tuyen-dung-popup .custom-input-file input + label:hover {
  color: #fff;
  background: #38A8D9;
}
.tuyen-dung-popup .custom-input-file input::-webkit-file-upload-button {
  visibility: hidden;
}

.btn-ung-tuyen-ngay, .btn-so-yeu-li-lich {
  margin-top: 10px;
  padding: 10px 30px;
  border: none;
  background: #00C5A3;
  font-weight: 700;
  color: #fff;
  outline: none;
  cursor: pointer;
  text-transform: uppercase;
  position: relative;
  z-index: 1;
}
.btn-ung-tuyen-ngay:before, .btn-so-yeu-li-lich:before {
  content: "";
  transition: all 0.2s ease-in-out;
  position: absolute;
  top: 5px;
  left: 5px;
  width: 100%;
  height: 100%;
  background: rgba(0, 197, 163, 0.25);
  z-index: -1;
}
.btn-ung-tuyen-ngay:hover, .btn-so-yeu-li-lich:hover {
  transition: all 0.3s ease-in-out;
  top: 5px;
  left: 5px;
}
.btn-ung-tuyen-ngay:hover:before, .btn-so-yeu-li-lich:hover:before {
  top: 0;
  left: 0;
}

.btn-so-yeu-li-lich {
  margin-left: 10px;
  background: #FBC087;
}
.btn-so-yeu-li-lich:before {
  background: rgba(251, 192, 135, 0.25);
}

